class Compiler {
  constructor (vm) {
    this.el = vm.$el
    this.vm = vm
    this.compiler(this.el)
  }
  // ÁºñËØëÊ®°ÊùøÔºåÂ§ÑÁêÜÊñáÊú¨ËäÇÁÇπÂíåÂÖÉÁ¥†ËäÇÁÇπ
  compiler (el) {
    let childNodes = el.childNodes
    Array.from(childNodes).forEach(node => {
      // Â§ÑÁêÜÊñáÊú¨ËäÇÁÇπ
      if (this.isTextNode(node)) {
        this.compileText(node)
      }else if (this.isElementNode(node)) {
        // Â§ÑÁêÜÂÖÉÁ¥†ËäÇÁÇπ
        this.compileElement(node)
      }

      // Âà§Êñ≠nodeËäÇÁÇπÊòØÂê¶ÊúâÂ≠êËäÇÁÇπÔºåÂ¶ÇÊûúÊúâÂ≠êËäÇÁÇπÔºåË¶ÅÈÄíÂΩíË∞ÉÁî®compile
      if (node.childNodes && node.childNodes.length) {
        this.compiler(node)
      }
    })
  }
  // ÁºñËØëÂÖÉÁ¥†ËäÇÁÇπÔºåÂ§ÑÁêÜÊåá‰ª§
  compileElement (node) {
    // console.dir(node)
    // ÈÅçÂéÜÊâÄÊúâÁöÑÂ±ûÊÄßËäÇÁÇπ
    Array.from(node.attributes).forEach(attr => {
      // Âà§Êñ≠ÊòØÂê¶ÊòØÊåá‰ª§
      let attrName = attr.name
      if (this.isDirective(attrName)) {
        // v-text --> text
        attrName = attrName.substr(2)
        let key = attr.value
        this.update(node, key, attrName)
      }
    })
  }
  update (node, key, attrName) {
    let updateFn = this[attrName + 'Updater']
    updateFn && updateFn.call(this, node, this.vm[key], key)
  }

  // Â§ÑÁêÜ v-text Êåá‰ª§
  textUpdater (node, value, key) {
    node.textContent = value
    new Watcher(this.vm, key, newValue => {
      node.textContent = newValue
    })

  }
  // v-model
  modelUpdater (node, value, key) {
    node.value = value
    new Watcher(this.vm, key, newValue => {
      node.value = newValue
    })
    // ÂèåÂêëÁªëÂÆö
    node.addEventListener('input', () => {
      this.vm[key] = node.value
    })
  }

  // ÁºñËØëÊñáÊú¨ËäÇÁÇπÔºåÂ§ÑÁêÜÂ∑ÆÂÄºË°®ËææÂºè
  compileText (node) {
    // {{ msg }}
    // Ê≠£ÂàôË°®ËææÂºè
    let reg = /\{\{( .+?)\}\}/
    let value = node.textContent
    if (reg.test(value)) { 
      let key = RegExp.$1.trim() // Ëé∑ÂèñÂåπÈÖçÊ≠£ÂàôÈáåÁöÑÂÄº
      node.textContent = value.replace(reg, this.vm[key])

      // cÂàõÂª∫watcherÂØπË±°ÔºåÂΩìÊï∞ÊçÆÊîπÂèò Êõ¥Êñ∞ËßÜÂõæ
      new Watcher(this.vm, key, (newValue) => {
        node.textContent = newValue
      })
    }
  }
  // Âà§Êñ≠ÂÖÉÁ¥†Â±ûÊÄßÊòØÂê¶ÊòØÊåá‰ª§
  isDirective (attrName) {
    return attrName.startsWith('v-')
  }
  // Âà§Êñ≠ËäÇÁÇπÊòØÂê¶ÊòØÊñáÊú¨ËäÇÁÇπ
  isTextNode (node) {
    return node.nodeType === 3
  }
  // Âà§Êñ≠ÂÖÉÁ¥†ËäÇÁÇπÊòØÂê¶ÊòØÂÖÉÁ¥†ËäÇÁÇπ
  isElementNode (node) {
    return node.nodeType === 1
  }
}